<template>
        <div class="img-view" @click="bigImg">
            <!-- 遮罩层 -->
            <div class="img-layer"></div>
            <div class="img">
                <img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+imgSrc">
            </div>
        </div>
</template>
<script>
export default {
    props: ['imgSrc'],
    data(){
    	return{
    		imgbase_url: imgbase_url 
    	}
    },
    methods: {
        bigImg() {
            // 发送事件
            this.$emit('clickit')
        }
    }
}
</script>
<style scoped>
/* bigimg */

.img-view {
    position: relative;
    width: 100%;
    height: 100%;
}

/*遮罩层样式*/
.img-view .img-layer {
    position: fixed;
    z-index: 1001;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
    overflow: hidden;
    transition: all .5s; 
}

/*不限制图片大小，实现居中*/
.img-view .img img {
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 1001;
    bottom: 0;
}
</style>


